@charset "UTF-8";
body,
html {
  overflow-x: hidden;
}
body.overflow,
html.overflow {
  overflow: hidden;
}

body {
  background: #F9F9F9;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  transition: 0.3s;
  display: inline-block;
}
a:hover {
  text-decoration: none;
}
a:focus {
  outline: none;
}

input {
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
}
input:focus {
  outline: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  transition: 0.3s;
}
button:focus {
  outline: none;
}

@keyframes rotate-animation {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.input-file-container {
