}

.input-file-trigger {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  transition: all 0.4s;
  cursor: pointer;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
}
.input-file-trigger .icon {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 50%;
