.checkbox-wrap label {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  font-family: "Inter", sans-serif;
}
.checkbox-wrap label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
}
.checkbox-wrap label input:checked ~ span {
  background: #fff;
}
.checkbox-wrap label input:checked ~ span:after {
  opacity: 1;
}
.checkbox-wrap label span {
  width: 24px;
  min-width: 24px;
  height: 24px;
  display: inline-block;
  position: relative;
  transition: 0.2s;
  border: 1px solid #d5dadf;
  border-radius: 2px;
  margin-right: 10px;
}
@media (max-width: 767px) {
  .checkbox-wrap label span {
    margin-right: 10px;
  }
}
.checkbox-wrap label span:after {
  content: "";
  background: url(../img/check-icon.svg) no-repeat center center/cover;
  width: 16px;
  height: 11px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -5px;
  margin-left: -7px;
  opacity: 0;
  transition: 0.2s;
}

